import React from 'react';
import { Link, NavLink } from "react-router-dom";
import './Navbar.css';

const Navbar = () => {
    return (

<nav className="navbar">
        <ul className='nav-list'>
            <li className='nav-item'>
                <NavLink activeclassName="active" className='nav-link' to="/"></NavLink>
            </li>
            <li className='nav-item'>
                <NavLink activeclassName="active" className='nav-link' to="/articles"></NavLink>
            </li>
            <li className='nav-item'>
                <NavLink activeclassName="active" className='nav-link' to="/recipes"></NavLink>
            </li>
            <li className='nav-item'>
                <NavLink activeclassName="active" className='nav-link' to="/books"> </NavLink>
            </li>
            <li className='nav-item'>
                <NavLink activeclassName="active" className='nav-link' to="/crafts">  </NavLink>
            </li>
            <li className='nav-item'>
                <NavLink activeclassName="active" className='nav-link' to="/words">  </NavLink>
            </li>
            <li className='nav-item'>
                <NavLink activeclassName="active" className='nav-link' to="/about"> </NavLink>
            </li>
        </ul>
</nav>
    )
};

export default Navbar;